.preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: var(--black);
  top: 0;
  left: 0;
  z-index: 40;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s all ease;
}
.preloader.active {
  opacity: 1;
  visibility: visible;
  animation: opacity 1s ease forwards;
  animation-delay: 2.3s;
}
.preloader.active .preloader__item {
  animation: scale-x 1s ease forwards;
  animation-delay: 2.3s;
}
.preloader.active .tile-1 {
  animation: flip-1 0.5s linear forwards;
}
.preloader.active .tile-2 {
  animation: flip-2 0.5s linear forwards;
  animation-delay: 0.5s;
}
.preloader.active .tile-3 {
  animation: flip-4 0.5s linear forwards;
  animation-delay: 1.5s;
}
.preloader.active .tile-4 {
  animation: flip-3 0.5s linear forwards;
  animation-delay: 1s;
}

.preloader__item {
  display: flex;
  flex-wrap: wrap;
  width: 60px;
  transform: rotate(-45deg);
  margin: auto;
  position: fixed;
  top: calc(50% - 30px);
  left: calc(50% - 30px);
  background-color: black;
}

.preloader__item span {
  flex-basis: 50%;
  aspect-ratio: 1/1;
  display: block;
  transition: opacity 0.7s ease;
}

.tile-1 {
  background-color: white;
  transform-origin: 100% 100%;
  border: none;
  opacity: 0;
  transition: opacity 0.7s ease;
}

.tile-2 {
  background-color: white;
  transform-origin: 50% 100%;
  opacity: 0;
}

.tile-3 {
  background-color: white;
  transform-origin: 50% 0%;
  opacity: 0;
}

.tile-4 {
  background-color: white;
  transform-origin: 0% 50%;
  opacity: 0;
}

@keyframes flip-1 {
  to {
    transform: rotateY(180deg);
    opacity: 1;
  }
}
@keyframes flip-2 {
  to {
    transform: rotateX(180deg);
    opacity: 1;
  }
}
@keyframes flip-3 {
  to {
    transform: rotateY(-180deg);
    opacity: 1;
  }
}
@keyframes flip-4 {
  to {
    transform: rotateX(-180deg);
    opacity: 1;
  }
}
@keyframes scale-x {
  to {
    transform: scale(50);
    opacity: 0;
  }
}
@keyframes opacity {
