.process__finale-wrapper {
  overflow: auto;
  flex-grow: 1;
}
.process__finale-label {
  row-gap: 0.625rem;
  max-width: 7.5rem;
}
.process__finale-total {
  align-self: end;
}
.process__notice {
  row-gap: 0.9375rem;
  align-self: center;
}
.process__unit {
  max-width: 14.375rem;
  display: flex;
  justify-content: space-between;
  gap: 0.625rem;
  flex-wrap: wrap;
}

.process__wrapper {
  overflow-x: auto;
  gap: 1.25rem;
  padding-bottom: 20px;
}
@media (max-width: 47.99875em) {
  .process__wrapper {
    overflow: initial;
  }
}

.process__result {
  position: relative;
  padding-top: 3.5625rem;
  background-repeat: no-repeat;
  min-height: 330px;
  display: grid;
}
.process__result img {
  max-height: 15.8125rem;
}
.process__result::after {
  content: "";
  position: absolute;
  width: calc(100% - 11px);
  height: 13px;
  background-color: #B9B7B7;
  bottom: 0px;
  transform: perspective(7px) rotateX(1deg);
  left: 5px;
}
.process__result-gap {
  align-items: center;
  grid-auto-flow: column;
  position: absolute;
  bottom: 20px;
  font-size: 12px;
  left: 50%;
  transform: translateX(-50%);
  padding-left: 20px;
}

.process__result-items {
  display: flex;
  padding-bottom: 45px;
  scrollbar-color: var(--primary) #cfcfcf;
  scrollbar-width: thin;
  min-height: 317px;
}
.process__result-items::-webkit-scrollbar {
  width: 5px;
}
.process__result-items::-webkit-scrollbar-track {
  border-radius: 0;
  background-color: #cfcfcf;
}
.process__result-items::-webkit-scrollbar-thumb {
  border-radius: 0;
  background-color: var(--primary);
}
.process__result-items::-webkit-scrollbar-thumb:hover {
  box-shadow: inset 1px 1px 40px #747373;
}

.process__result-item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 0.625rem;
  flex-shrink: 0;
  cursor: move;
}
.process__result-item::after, .process__result-item::before {
  content: "";
  position: absolute;
  bottom: 11px;
  width: 0;
  height: 0;
}
.process__result-item::before {
  left: 3px;
  border-top: 3px solid transparent;
  border-bottom: 3px solid transparent;
  border-right: 3px solid black;
}
.process__result-item::after {
  right: 3px;
  border-top: 3px solid transparent;
  border-bottom: 3px solid transparent;
  border-left: 3px solid black;
}
.process__result-item .line {
  position: absolute;
  width: calc(100% - 10px);
  height: 1px;
  background-color: black;
  bottom: 13.5px;
}

.process__result-metric {
  font-size: 0.5rem;
}

.topshelf {
  position: absolute;
  width: 100%;
  height: 50px;
  top: 0;
  border: 4px solid #DEDEDE;
}
.topshelf::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  border: 8px solid #C8C6C6;
}

.empty-space {
  position: relative;
  width: 100%;
  height: 253px;
  border-right: 7px solid #DEDEDE;
  border-left: 7px solid #DEDEDE;
  border-top: 4px solid #DEDEDE;
  background-color: #DDDDDD;
  border-bottom: 5px solid #DEDEDE;
  cursor: move;
}
.empty-space::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  border-right: 14px solid #C8C6C6;
  border-left: 14px solid #C8C6C6;
  border-top: 4px solid #C8C6C6;
  border-bottom: 14px solid #C8C6C6;
}
.empty-space::before {
  content: "";
  width: 100%;
  height: 8px;
  background: linear-gradient(180deg, #ADADAD 9.37%, #FFFFFF 34.9%, #D5D4D4 65.62%, #A6A5A5 100%);
  position: absolute;
  top: 36px;
}

.process__finale-img-items {
  display: flex;
  position: relative;
  padding: 30px 0px 30px 0px;
}

.process__pointer {
  text-align: center;
}
.process__pointer-vertical {
  width: calc(100% - 72px);
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
}
.process__pointer-top {
  border-block-start: 5px solid #D9DADC;
  padding-top: 3px;
  top: 0;
}
.process__pointer-bottom {
  border-block-end: 5px solid #D9DADC;
  padding-bottom: 3px;
  bottom: 0;
}
.process__pointer-right {
  border-block-start: 1px solid black;
  writing-mode: vertical-lr;
  margin-left: 15px;
}
.process__pointer-left {
  border-block-end: 1px solid black;
  writing-mode: vertical-rl;
  margin-right: 15px;
}